/*slider*/
/* 슬릭슬라이드 깜박임 해결 : 브라우즈 업그레이드와 관련있는 이슈였음
... reset으로 옮길 예정 
cloned 가 생성 되는 시간보다 변하는 시간이 길어서 ... 가 원인... */

.slick-track,
.slick-list {
  transition-duration: 0.5ms;
  /* transition-delay: 30ms; */
}
.MainVisual {
  position: relative;
  transition: 0.5s;
}

.MainVisual.on {
  padding: 0 0 0 0;
}

.MainVisual .arrows li {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 60px;
  height: 60px;
  color: #fff;
  font-size: 14px;
  font-weight: 300;
  cursor: pointer;
  background: transparent;
  border: none;
  outline: none;
  font-size: 0;
}

.MainVisual .arrows li::after {
  content: "";
  position: absolute;
  top: 50%;
  width: 20px;
  height: 20px;
  border-top: 1px solid #f9f9f9;
}

.MainVisual .arrows li::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  width: 60px;
  height: 0;
  border-top: 1px solid #f9f9f9;
}

.MainVisual .arrows li:first-child::after {
  left: 0;
  transform: rotate(-45deg);
  transform-origin: left top;
}

.MainVisual .arrows li:last-child::after {
  right: 0;
  transform: rotate(45deg);
  transform-origin: right top;
}

.MainVisual .arrows li:first-child {
  margin: 0 0 0 -800px;
}

.MainVisual .arrows li:last-child {
  margin: 0 0 0 800px;
}

.MainVisual .num {
  position: absolute;
  bottom: 250px;
  left: 50%;
  margin: 0 0 0 -600px;
  color: #fff;
  font-size: 35px;
}

.MainVisual .num span {
  font-size: 20px;
  color: #ddd;
}

.MainVisual .dots {
  position: absolute;
  top: 50%;
  left: 50%;
  margin: 0 0 0 600px;
  transform: translate(-100%, -50%);
  color: #f9f9f9;
  font-size: 18px;
  font-weight: 300;
  text-align: right;
}

.MainVisual .dots li {
  margin: 0 0 10px 0;
  cursor: pointer;
}

.MainVisual .dots li {
  font-size: 0;
}

.MainVisual .dots li.on {
  font-size: 18px;
}

.MainVisual .dots li::after {
  content: "";
  display: inline-block;
  width: 20px;
  height: 20px;
  border: 1px solid rgba(255, 255, 255, 0.5);
  margin: 0 0 0 15px;
  vertical-align: -0.2em;
}

.MainVisual .dots li.on::after {
  background: #fff;
}
.main_slider figure {
  position: relative;
  height: 900px;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}

.main_slider figure .inner {
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-shadow: 0 0 3px #000;
  color: #fff;
  max-width: 1200px;
  width: 100%;
}

.main_slider .item01 {
  background-image: url(../public/assets/main_visual01.jpg);
}

.main_slider .item02 {
  background-image: url(../public/assets/main_visual02.jpg);
}

.main_slider .item03 {
  background-image: url(../public/assets/main_visual03.jpg);
}

.main_slider figure h2 {
  font-size: 50px;
  font-weight: 900;
  margin: 0 0 20px 0;
  transform: translateY(100px);
  opacity: 0;
}

.main_slider figure.on h2 {
  transform: translateY(0);
  opacity: 1;
  transition: 0.5s;
}

.main_slider figure span {
  display: block;
  margin: 10px 0;
}

.main_slider figure p {
  font-size: 18px;
  font-weight: 300;
  line-height: 1.45;
  margin: 0 0 50px 0;
  transform: translateY(100px);
  opacity: 0;
}

.main_slider figure.on p {
  transform: translateY(0);
  opacity: 1;
  transition: 0.5s 0.5s;
}

.main_slider figure a {
  display: inline-block;
  padding: 10px 25px;
  border: 1px solid #f9f9f9;
  color: #f9f9f9;
  font-size: 13px;
  transform: translateY(100px);
  opacity: 0;
}

.main_slider figure.on a {
  transform: translateY(0);
  opacity: 1;
  transition: 0.5s 1s;
}
